import Vue from "vue";
import Vuex from "vuex";
import http from "@/infra/communication/http";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    token: "",
    isLogged: false,
    isADM: false
  },
  mutations: {
    auth_success(state, data) {
      state.token = data.token;
      state.isLogged = true;
      state.isADM = data.adm;
      debugger
      http.setToken(data.token);
    },
    logoff(state){      
        debugger      
        state.token = '';
        state.isLogged = false;
        state.isADM = false;        
        http.setToken('');      
    }
  },
  actions: {
    executarLogin({ commit, dispatch }, data) {
      commit("auth_success", data);
    },
    executarLogoff({ commit, dispatch }) {
      commit("logoff");
    }
  },
  getters: {
    isLogged: state => state.isLogged,
    isADM: state => state.isADM
  }
});

export default store;
