import Vue from 'vue'

export function showMensagemErro(e) {
    if (e && e.response && e.response.data) {
        Vue.toasted.global.defaultError({msg : e.response.data})
    } else if (typeof e === 'string') {
        Vue.toasted.global.defaultError({msg : e})
    } else {
        Vue.toasted.global.defaultError()        
    }
}

export function showMensagemSucesso(e) {
    Vue.toasted.global.defaultSuccess({msg : e}) 
}

export function showMensagemInformacao(e) {
    Vue.toasted.global.defaultInfo({msg : e}) 
}

export default { showMensagemErro, showMensagemSucesso, showMensagemInformacao}