import DashboardLayout from "@/pages/Dashboard/Layout/DashboardLayout.vue";
import AuthLayout from "@/pages/Dashboard/Pages/AuthLayout.vue";
import Vue from "vue";

// Dashboard pages
import Dashboard from "@/pages/Dashboard/Dashboard.vue";

// // Pages
import Login from "@/pages/Dashboard/Pages/Login.vue";
import LocaisLista from "@/pages/Local/Lista.vue";
import LocaisEditar from "@/pages/Local/Cadastro.vue";

import ContasLista from "@/pages/Conta/Lista.vue";
import ContasEditar from "@/pages/Conta/Cadastro.vue";

import PlacasLista from "@/pages/Placa/Lista.vue";
import PlacasEditar from "@/pages/Placa/Cadastro.vue";

import UsuariosLista from "@/pages/Usuario/Lista.vue";
import UsuariosEditar from "@/pages/Usuario/Cadastro.vue";

import PlacaUsuariosLista from "@/pages/PlacaUsuario/Lista.vue";
import PlacaUsuariosEditar from "@/pages/PlacaUsuario/Cadastro.vue";

import PlacaAgendamentosLista from "@/pages/PlacaAgendamentos/Lista.vue";
import PlacaAgendamentosEditar from "@/pages/PlacaAgendamentos/Cadastro.vue";

import MaquinasLista from "@/pages/Maquina/Lista.vue";
import MaquinasEditar from "@/pages/Maquina/Cadastro.vue";

import MotivosLista from "@/pages/Motivo/Lista.vue";
import MotivosEditar from "@/pages/Motivo/Cadastro.vue";


import ParadasLista from "@/pages/Parada/Lista.vue";
import ParadasEditar from "@/pages/Parada/Cadastro.vue";

import Router from "vue-router";
import store from "@/infra/store/store";
Vue.use(Router);

let authPages = {
  path: "/",
  component: AuthLayout,
  name: "Authentication",
  children: [
    {
      path: "/login",
      name: "Login",
      component: Login,
      meta: {
        anonymous: true
      }
    }
  ]
};

var routes = new Router({
  linkExactActiveClass: "active",
  routes: [
    {
      path: "/",
      redirect: "/login",
      name: "Login"
    },
    authPages,
    {
      path: "/",
      component: DashboardLayout,
      children: [
        {
          path: "dashboard",
          name: "Dashboard",
          components: { default: Dashboard }
        },
        {
          path: "/conta/:idConta/locais",
          name: "Locais",
          components: { default: LocaisLista }
        },
        {
          path: "Conta/:idConta/Local/:id",
          components: { default: LocaisEditar },
          props: true
        },
        {
          path: "Conta/:idConta/Local",
          components: { default: LocaisEditar }
        },
        {
          path: "/contas",
          name: "Contas",
          components: { default: ContasLista }
        },
        {
          path: "/Conta/:id",
          components: { default: ContasEditar },
          props: true
        },
        {
          path: "/Conta",
          components: { default: ContasEditar }
        },
        {
          path: "/conta/:idConta/local/:idLocal/placas",
          name: "Placas",
          components: { default: PlacasLista }
        },
        {
          path: "/Conta/:idConta/Local/:idLocal/Placa/:id",
          components: { default: PlacasEditar },
          props: true
        },
        {
          path: "/Conta/:idConta/Local/:idLocal/Placa",
          components: { default: PlacasEditar }
        },
        {
          path: "/usuarios",
          name: "Usuarios",
          components: { default: UsuariosLista }
        },
        {
          path: "Usuario/:id",
          components: { default: UsuariosEditar },
          props: true
        },
        {
           path: "/Usuario",
           components: { default: UsuariosEditar }
         },
         {
          path: "/maquinas",
          name: "Maquinas",
          components: { default: MaquinasLista }
        },
        {
          path: "Maquina/:id",
          components: { default: MaquinasEditar },
          props: true
        },
        {
           path: "/Maquina",
           components: { default: MaquinasEditar }
         },

         {
          path: "/Motivos",
          name: "Motivos",
          components: { default: MotivosLista }
        },
        {
          path: "Motivo/:id",
          components: { default: MotivosEditar },
          props: true
        },
        {
           path: "/Motivo",
           components: { default: MotivosEditar }
         },

         {
          path: "/Paradas",
          name: "Paradas",
          components: { default: ParadasLista }
        },
        {
          path: "Parada/:id",
          components: { default: ParadasEditar },
          props: true
        },
        {
           path: "/Parada",
           components: { default: ParadasEditar }
         },
         
        {
          path: "/conta/:idConta/local/:idLocal/placa/:idPlaca/placaUsuarios",
          name: "PlacaUsuarios",
          components: { default: PlacaUsuariosLista }
        },
        {
          path:
            "/Conta/:idConta/Local/:idLocal/Placa/:idPlaca/placaUsuario/:id",
          components: { default: PlacaUsuariosEditar },
          props: true
        },
        {
          path: "/Conta/:idConta/Local/:idLocal/Placa/:idPlaca/placaUsuario",
          components: { default: PlacaUsuariosEditar }
        },
        {
          path: "/conta/:idConta/local/:idLocal/placa/:idPlaca/placaAgendamentos",
          name: "PlacaUsuarios",
          components: { default: PlacaAgendamentosLista }
        },
        {
          path:
            "/Conta/:idConta/Local/:idLocal/Placa/:idPlaca/placaAgendamento/:id",
          components: { default: PlacaAgendamentosEditar },
          props: true
        },
        {
          path: "/Conta/:idConta/Local/:idLocal/Placa/:idPlaca/placaAgendamento",
          components: { default: PlacaAgendamentosEditar }
        }
      ]
    }
  ]
});

routes.beforeEach((to, from, next) => {
  if (!to.matched.some(record => record.meta.anonymous)) {
    if (store.getters.isLogged) {
      next();
      return;
    }
    next("/login");
  } else {
    next();
  }
});

export default routes;
