<template>
  <div>
    <md-card>
      <md-card-header class="md-card-header-icon md-card-header-green">
        <div class="card-icon">
          <md-icon>person_add</md-icon>
        </div>
        <h4 class="title">
          Agendamentos -
          <small>Informe os dados</small>
        </h4>
      </md-card-header>

      <md-card-content>        
        <div class="md-layout">          
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Nome</label>
              <md-input v-model="info.nome" type="text"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Dia da Semana</label>
              <!-- <md-input v-model="info.diaSemana" type="text"></md-input> -->
              <md-select v-model="info.diaSemana" name="diaSemana" id="diaSemana">
                <md-option
                  v-for="item in diasDaSenama"
                  :key="item.id"
                  :value="item.id"
                >{{ item.nome }}</md-option>
              </md-select>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Hora Abertura</label>
              <md-input v-model="info.horaAbertura" type="time"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Hora Fechamento</label>
              <md-input v-model="info.horaFechamento" type="time"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-size-100 text-right">
            <md-button @click="Cancelar()" class="md-raised md-round md-danger mt-4">Cancelar</md-button>
            <md-button @click="Salvar()" class="md-raised md-round md-success mt-4">Salvar</md-button>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import api from "@/infra/communication/api.js";
export default {  
  data() {
    return {
      id: this.$route.params.id,
      info: {
        id: 0,
        nome: "",
        localPlacaId: 0,
        diaSemana: 0,
        horaAbertura: "",
        horaFechamento: ""
      },
      erros: [],
      type: ["", "info", "success", "warning", "danger"],
      contas: [],
      notifications: {
        topCenter: false
      },
      diasDaSenama:[
        {id:0, nome: "Domingo"},
        {id:1, nome: "Segunda"},
        {id:2, nome: "Terça"},
        {id:3, nome: "Quarta"},
        {id:4, nome: "Quinta"},
        {id:5, nome: "Sexta"},
        {id:6, nome: "Sábado"}
      ]
    };
  },
  watch: {
    $route(to, from) {
      if (this.$route.params.id > 0) {
        this.id = this.$route.params.id;
        let r = api.cadastros.placaAgendamentos.retornarPorId(this.id);
        if (r.sucesso) {
          this.info = r.data;
        } else {
          // this.$toasted.global.defaultError();
        }
      }
    }
  },
  async mounted() {
    if (this.$route.params.id > 0) {
      this.id = this.$route.params.id;
      let r = await api.cadastros.placaAgendamentos.retornarPorId(this.id);
      if (r.sucesso) {
        this.info = r.data;
      } else {
        // this.$toasted.global.defaultError();
      }
    }        
  },
  methods: {
    async Salvar() {
      this.erros = [];
      // if (this.info.nome == "") {
      //   this.erros.push({ msg: "Campo Nome é obrigatório. verifique!" });
      //   this.notifyVue(
      //     "botton",
      //     "center",
      //     "Campo Nome é obrigatório. verifique!",
      //     "danger"
      //   );
      // }

      if (!this.erros.length) {
        this.info.localPlacaId = this.$route.params.idPlaca;
        let r = await api.cadastros.placaAgendamentos.salvar(this.info);
        if (r.sucesso) {
          this.$router.push(
            `/conta/${this.$route.params.idConta}/Local/${this.$route.params.idLocal}/placa/${this.$route.params.idLocal}/placaAgendamentos`
          );
        } else {
          // this.$toasted.global.defaultError();
        }
      }
    },
    Cancelar() {
      this.$router.push(
        `/conta/${this.$route.params.idConta}/Local/${this.$route.params.idLocal}/placa/${this.$route.params.idLocal}/placaAgendamentos`
      );
    },
    notifyVue(verticalAlign, horizontalAlign, message, tipo) {
      var color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: tipo
      });
    }
  }
};
</script>
<style></style>
