<template>
  <div class="md-layout text-center">
    <div
      class="md-layout-item md-size-33 md-medium-size-50 md-small-size-70 md-xsmall-size-100"
    >
      <login-card header-color="blue">
        <h4 slot="title" class="title"> SGPMI</h4>
            <img  slot="title" src="./Logo.png">
        <!-- <md-button
          slot="buttons"
          href="#facebook"
          class="md-just-icon md-simple md-white"
        >
          <i class="fab fa-facebook-square"></i>
        </md-button>
        <md-button
          slot="buttons"
          href="#twitter"
          class="md-just-icon md-simple md-white"
        >
          <i class="fab fa-twitter"></i>
        </md-button>
        <md-button
          slot="buttons"
          href="#google"
          class="md-just-icon md-simple md-white"
        >
          <i class="fab fa-google-plus-g"></i>
        </md-button> -->
        <md-field class="md-form-group" slot="inputs">
          <md-icon>email</md-icon>
          <label>Email...</label>
          <md-input v-model="email" type="email"></md-input>
        </md-field>
        <md-field class="md-form-group" slot="inputs">
          <md-icon>lock_outline</md-icon>
          <label>Senha...</label>
          <md-input type="password" v-model="password"></md-input>
        </md-field>
        <md-button
          slot="footer"
          class="md-simple md-dark md-lg"
          @click="logar()"
        >
          Entrar
        </md-button>
      </login-card>
    </div>
  </div>
</template>
<script>
import api from "@/infra/communication/api.js";
import store from "@/infra/store/store";
import { LoginCard } from "@/components";
export default {
  components: {
    LoginCard
  },
  data() {
    return {
      email: null,
      password: null
    };
  },
  methods: {
    async logar() {
      let result = await api.login.authenticate(this.email, this.password);
      if (result.sucesso) {
        console.log(result.data);
        store.dispatch("executarLogin", result.data);
        this.$router.replace("/Dashboard");
      } else {
        this.$toasted.global.defaultError({msg: "Login errado"});
      }
    }
  },
  mounted(){
    store.dispatch("executarLogoff");
    this.$router.replace("/login");
  }
};
</script>

