import Vue from 'vue'
import Toasted from 'vue-toasted'

Vue.use(Toasted, {
    iconPack: 'fontawesome',
    duration: 3000,
    fullWidth: true,    
    position: 'top-center',
    keepOnHover: true,
    closeOnSwipe: false,
    action : [
        {
            text : 'fechar',
            onClick : (e, toastObject) => {
                toastObject.goAway(0);
            }
        }
    ]
})

Vue.toasted.register(
    'defaultSuccess',
    payload => {
        return !payload.msg? 'Operação realizada com sucesso!' : payload.msg
    },
    {type: 'success', icon: 'check-circle'}
)

Vue.toasted.register(
    'defaultError',
    payload => {       
        let msg = ''
        
        if (payload.msg && typeof payload.msg === 'string')
            msg = payload.msg            

        if (typeof payload.msg == 'object') {
            if (payload.msg.mensagem)
                msg = msg + ' ' + payload.msg.mensagem 
        
            if (payload.msg.detalheMsg)
                msg = msg + ' ' + payload.msg.detalheMsg

            /*if (payload.msg.detalheMsgTecnico)
                msg = msg + ' ' + payload.msg.detalheMsgTecnico            
            */            
        }   
        
        if (msg === '')
            msg = 'Oops.. Erro inesperado.'
                
        return msg        
    },
    {
        type: 'error', 
        icon: 'times-circle', 
        duration: null
    }
)

Vue.toasted.register(
    'defaultInfo',
    payload => {
        return !payload.msg? 'Atenção.' : payload.msg
    },
    {type: 'info', icon: 'info-circle'}
)