<template>
  <div class="md-layout">
    <div class="md-layout-item">
      <md-card>
        <md-card-header class="md-card-header-icon md-card-header-green">
          <div class="card-icon">
            <md-icon>touch_app</md-icon>
          </div>
          <h4 class="title">Máquinas</h4>
          <md-button @click="novo()" class="md-raised md-round md-success mt-4"
            >Adicionar</md-button
          >
        </md-card-header>
        <md-card-content>
          <md-table
            :value="queriedData"
            :md-sort.sync="currentSort"
            :md-sort-order.sync="currentSortOrder"
            :md-sort-fn="customSort"
            class="paginated-table table-striped table-hover"
          >
            <md-table-toolbar>
              <md-field>
                <label for="pages">Por Página</label>
                <md-select v-model="pagination.perPage" name="pages">
                  <md-option
                    v-for="item in pagination.perPageOptions"
                    :key="item"
                    :label="item"
                    :value="item"
                    >{{ item }}</md-option
                  >
                </md-select>
              </md-field>

              <md-field>
                <md-input
                  type="search"
                  class="mb-3"
                  clearable
                  style="width: 200px"
                  placeholder="Pesquisar"
                  v-model="searchQuery"
                ></md-input>
              </md-field>
            </md-table-toolbar>

            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Nome" md-sort-by="nome">
                {{ item.nome }}
              </md-table-cell>

              <md-table-cell md-label>
                <md-button
                  class="md-just-icon md-success md-simple"
                  @click.native="handleEdit(item.id)"
                >
                  <md-icon>dvr</md-icon>
                  <md-tooltip md-direction="top">Editar</md-tooltip>
                </md-button>
                <md-button
                  class="md-just-icon md-success md-simple ml-2"
                  @click="handleDelete(item)"
                >
                  <md-icon>close</md-icon>
                  <md-tooltip md-direction="top">Excluir</md-tooltip>
                </md-button>
              </md-table-cell>
            </md-table-row>
          </md-table>
        </md-card-content>
        <div class="text-center" v-show="carregando">
          <b-spinner label="Carregando..."></b-spinner>
        </div>
        <md-card-actions md-alignment="space-between">
          <div class>
            <p class="card-category">
              Mostrando {{ from + 1 }} ao {{ to }} de {{ total }} registros
            </p>
          </div>          
          <pagination
            class="pagination-no-border pagination-success"
            v-model="pagination.currentPage"
            :per-page="pagination.perPage"
            :total="total"
          ></pagination>
                   </md-card-actions>
      </md-card>
    </div>
  </div>
</template>

<script>
import { Pagination } from "@/components";
import Fuse from "fuse.js";
import Swal from "sweetalert2";
import api from "@/infra/communication/api.js";

export default {
  components: {
    Pagination
  },
  computed: {
    /***
     * Returns a page from the searched data or the whole data. Search is performed in the watch section below
     */
    queriedData() {
      let result = this.tableData;
      if (this.searchedData.length > 0) {
        result = this.searchedData;
      }
      return result.slice(this.from, this.to);
    },
    to() {
      let highBound = this.from + this.pagination.perPage;
      if (this.total < highBound) {
        highBound = this.total;
      }
      return highBound;
    },
    from() {
      return this.pagination.perPage * (this.pagination.currentPage - 1);
    },
    total() {
      return this.searchedData.length > 0
        ? this.searchedData.length
        : this.tableData.length;
    }
  },
  data() {
    return {
      carregando: true,
      currentSort: "nome",
      currentSortOrder: "asc",
      pagination: {
        perPage: 5,
        currentPage: 1,
        perPageOptions: [5, 10, 25, 50],
        total: 0
      },
      searchQuery: "",
      propsToSearch: ["nome"],
      tableData: [],
      searchedData: [],
      fuseSearch: null
    };
  },
  methods: {
    customSort(value) {
      return value.sort((a, b) => {
        const sortBy = this.currentSort;
        if (this.currentSortOrder === "desc") {
          return a[sortBy].localeCompare(b[sortBy]);
        }
        return b[sortBy].localeCompare(a[sortBy]);
      });
    },
    handleLike(item) {
      Swal.fire({
        title: `You liked ${item.name}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "md-button md-success"
      });
    },
    handleEdit(item) {
      this.$router.push(`/maquina/${item}`);
    },
    async handleDelete(item) {
      await Swal.fire({
        title: "Você tem Certeza?",
        text: `Você não poderá reverter essa operação!`,
        type: "warning",
        showCancelButton: true,
        confirmButtonClass: "md-button md-success btn-fill",
        cancelButtonClass: "md-button md-danger btn-fill",
        confirmButtonText: "Sim, Exclua!",
        cancelButtonText: "Cancelar!",
        buttonsStyling: false
      }).then(result => {
        if (result.value) {
          this.delete(item);
        }
      });
    },
    async delete(item) {
      let r = await api.cadastros.maquinas.excluir(item.id);
      if (r.sucesso) {
        this.deleteRow(item);
        Swal.fire({
          title: "Excluído!",
          text: `Você deletou ${item.nome}`,
          type: "success",
          confirmButtonClass: "md-button md-success btn-fill",
          buttonsStyling: false
        });
      } else {
        // this.$toasted.global.defaultError();
      }
    },
    deleteRow(item) {
      let indexToDelete = this.tableData.findIndex(
        tableRow => tableRow.id === item.id
      );
      if (indexToDelete >= 0) {
        this.tableData.splice(indexToDelete, 1);
      }
    },
    novo() {
      this.$router.push(`/Maquina`);
    },
  },
  async mounted() {
    let r = await api.cadastros.maquinas.retornarVarios(
    );
    if (r.sucesso) {
      this.tableData = r.data;
    } else {
      // this.$toasted.global.defaultError();
    }
    this.carregando = false;
  },
  watch: {
    searchQuery(value) {
      this.fuseSearch = new Fuse(this.tableData, {
        keys: ["nome"],
        threshold: 0.3
      });
      let result = this.tableData;
      if (value !== "") {
        result = this.fuseSearch.search(this.searchQuery);
      }
      this.searchedData = result;
    }
  }
};
</script>

<style lang="css" scoped>
.md-card .md-card-actions {
  border: 0;
  margin-left: 20px;
  margin-right: 20px;
}
</style>
