import Vue from "vue";
import DashboardPlugin from "./material-dashboard";

import store from "@/infra/store/store";

// Plugins
import App from "./App.vue";
import Chartist from "chartist";

// router setup
import router from "./routes/routes";

import BootstrapVue from "bootstrap-vue";

import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import './infra/mensagens/mensagensVueToasted'
import moment from 'moment'


// plugin setup
Vue.use(DashboardPlugin);

import VueTheMask from "vue-the-mask";
Vue.use(VueTheMask);
Vue.use(BootstrapVue);

Object.defineProperty(Vue.prototype, "$Chartist", {
  get() {
    return this.$root.Chartist;
  }
});

Vue.filter('hora', function(valor) {
  const arr = valor.split('')
  arr.splice(2, 0, ':')
  arr.splice(4, 0, ':')
  return arr.join('')
})

Vue.config.productionTip = false
Vue.filter('formatDate', function(value) {
  if (value) {
    return moment(String(value)).format('DD/MM/YYYY hh:mm:ss')
  }
});


  Vue.material.locale.shortMonths  = ['Jan', 'Fev', 'Mar', 'Abril','Maio', 'Jun', 'Jul','Ago', 'Set', 'Out','Nov','Dez'];
  Vue.material.locale.Months  = ['Janeiro', 'Fevereiro', 'Março', 'Abril','Maio', 'Junho', 'Julho','Agosto', 'Setembro', 'Outubro','Novembro','Dezembro'];
  Vue.material.locale.days = ['Domingo','Segunda', 'Terça', 'Quarta', 'Quinta','Sexta', 'Sabado'];
  Vue.material.locale.shortDays = ['Dom', 'Seg', 'Ter', 'Qua', 'Qui','Sex','Sab'];
  Vue.material.locale.shorterDays = ['D', 'S', 'T', 'Q', 'Q', 'S', 'S'],

 Vue.material.locale.dateFormat = 'dd/MM/yyyy'

/* eslint-disable no-new */
new Vue({
  store,
  el: "#app",
  render: h => h(App),
  router,

  data: {
    Chartist: Chartist
  }
});
