<template>
  <div>
    <md-card>
      <md-card-header class="md-card-header-icon md-card-header-green">
        <div class="card-icon">
          <md-icon>wifi</md-icon>
        </div>
        <h4 class="title">
          Placa -
          <small>Informe os dados</small>
        </h4>
      </md-card-header>

      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Nome</label>
              <md-input v-model="info.nome" type="text"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-10 md-size-25">
            <md-switch v-model="info.ativa" class="md-primary">Ativo</md-switch>
          </div>

          <div class="md-layout-item md-size-100 text-right">
            <md-button
              @click="Cancelar()"
              class="md-raised md-round md-danger mt-4"
              >Cancelar</md-button
            >
            <md-button
              @click="Salvar()"
              class="md-raised md-round md-success mt-4"
              >Salvar</md-button
            >
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import api from "@/infra/communication/api.js";
export default {
  data() {
    return {
      id: this.$route.params.id,
      info: {
        id: 0,
        nome: "",
        //guid: ,
        iPExterno: "",
        //dataHoraUltAtualizacao: null,
        situacao: 1,
        ativa: true,
        localId: null
      },
      erros: [],
      type: ["", "info", "success", "warning", "danger"],
      contas: [],
      notifications: {
        topCenter: false
      }
    };
  },
  watch: {
    $route(to, from) {
      if (this.$route.params.id > 0) {
        this.id = this.$route.params.id;
        let r = api.cadastros.placas.retornarPorId(this.id);
        if (r.sucesso) {
          this.info = r.data;
        } else {
          // this.$toasted.global.defaultError();
        }
      }
    }
  },
  async mounted() {
    if (this.$route.params.id > 0) {
      this.id = this.$route.params.id;
      let r = await api.cadastros.placas.retornarPorId(this.id);
      if (r.sucesso) {
        this.info = r.data;
      } else {
        // this.$toasted.global.defaultError();
      }
    }
  },
  methods: {
    async Salvar() {
      this.erros = [];
      if (this.info.nome == "") {
        this.erros.push({ msg: "Campo Nome é obrigatório. verifique!" });
        this.notifyVue(
          "botton",
          "center",
          "Campo Nome é obrigatório. verifique!",
          "danger"
        );
      }

      if (!this.erros.length) {
        debugger;
        this.info.localId = this.$route.params.idLocal;
        let r = await api.cadastros.placas.salvar(this.info);
        if (r.sucesso) {
          this.$router.push(
            `/conta/${this.$route.params.idConta}/Local/${this.$route.params.idLocal}/placas`
          );
        } else {
          // this.$toasted.global.defaultError();
        }
      }
    },
    Cancelar() {
      this.$router.push(
        `/conta/${this.$route.params.idConta}/Local/${this.$route.params.idLocal}/placas`
      );
    },
    notifyVue(verticalAlign, horizontalAlign, message, tipo) {
      var color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: tipo
      });
    }
  }
};
</script>
<style></style>
