<template>
  <div>
    <md-card>
      <md-card-header class="md-card-header-icon md-card-header-green">
        <div class="card-icon">
          <md-icon>person_add</md-icon>
        </div>
        <h4 class="title">
          Vinculação Placa e Usuários -
          <small>Informe os dados</small>
        </h4>
      </md-card-header>

      <md-card-content>
        <!-- usuarioId
        localPlacaId
        dataHoraUltUso
        dataHoraInicialUso
        dataHoraFinalUso
        ativo-->
        <div class="md-layout">
          <div class="md-layout-item md-small-size-10 md-size-50">
            <md-field>
              <label for="usuario">Usuario</label>
              <md-select v-model="info.usuarioId" name="usuario" id="usuario">
                <md-option
                  v-for="item in usuarios"
                  :key="item.id"
                  :value="item.id"
                >{{ item.nomeCompleto }}</md-option>
              </md-select>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-10 md-size-25">
            <md-switch v-model="info.ativo" class="md-primary">Ativo</md-switch>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-datepicker v-model="info.dataInicialUso" md-immediately lang="pt-br">
              <label>Selecione a data inicial</label>
            </md-datepicker>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Hora Inicial</label>
              <md-input v-model="info.horaInicialUso" type="time"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-datepicker v-model="info.dataFinalUso" md-immediately>
              <label>Selecione a data final</label>
            </md-datepicker>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label>Hora Final</label>
              <md-input v-model="info.horaFinalUso" type="time"></md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-size-100 text-right">
            <md-button @click="Cancelar()" class="md-raised md-round md-danger mt-4">Cancelar</md-button>
            <md-button @click="Salvar()" class="md-raised md-round md-success mt-4">Salvar</md-button>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import api from "@/infra/communication/api.js";
export default {
  data() {
     let dateFormat = this.$material.locale.dateFormat || 'dd-MM-yyyy'
      let now = new Date()

    return {
      id: this.$route.params.id,
      info: {
        id: 0,
        usuarioId: 0,
        localPlacaId: 0,
        dataHoraUltUso: now,
        dataInicialUso: now,
        horaInicialUso: null,
        dataFinalUso: now,
        horaFinalUso: null,
        ativo: true
      },
      usuarios: [],
      erros: [],
      type: ["", "info", "success", "warning", "danger"],
      contas: [],
      notifications: {
        topCenter: false
      }
    };
  },
  watch: {
    $route(to, from) {
      if (this.$route.params.id > 0) {
        this.id = this.$route.params.id;
        let r = api.cadastros.placaUsuarios.retornarPorId(this.id);
        if (r.sucesso) {
          this.info = r.data;
        } else {
          // this.$toasted.global.defaultError();
        }
      }
    }
  },
  async mounted() {
    if (this.$route.params.id > 0) {
      this.id = this.$route.params.id;
      let r = await api.cadastros.placaUsuarios.retornarPorId(this.id);
      if (r.sucesso) {
        this.info = r.data;
      } else {
        // this.$toasted.global.defaultError();
      }
    }
    let r = await api.cadastros.usuarios.retornarPorConta(
      this.$route.params.idConta
    );
    if (r.sucesso) {
      this.usuarios = r.data;
    } else {
      // this.$toasted.global.defaultError();
    }
  },
  methods: {
    async Salvar() {
      this.erros = [];
      // if (this.info.nome == "") {
      //   this.erros.push({ msg: "Campo Nome é obrigatório. verifique!" });
      //   this.notifyVue(
      //     "botton",
      //     "center",
      //     "Campo Nome é obrigatório. verifique!",
      //     "danger"
      //   );
      // }

      if (!this.erros.length) {
        this.info.localPlacaId = this.$route.params.idPlaca;
        let r = await api.cadastros.placaUsuarios.salvar(this.info);
        if (r.sucesso) {
          this.$router.push(
            `/conta/${this.$route.params.idConta}/Local/${this.$route.params.idLocal}/placa/${this.$route.params.idLocal}/placaUsuarios`
          );
        } else {
          // this.$toasted.global.defaultError();
        }
      }
    },
    Cancelar() {
      this.$router.push(
        `/conta/${this.$route.params.idConta}/Local/${this.$route.params.idLocal}/placa/${this.$route.params.idLocal}/placaUsuarios`
      );
    },
    notifyVue(verticalAlign, horizontalAlign, message, tipo) {
      var color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: tipo
      });
    }
  }
};
</script>
<style></style>
